<template>
  <div class="day-div" :id=getId(i) :style="cssProps">
    <h1>{{day}} {{i+1}}</h1>
    <p>{{text}}</p>
  </div>
</template>

<style scoped>
div{
  background-image: linear-gradient(-225deg, rgba(0, 0, 0, 0.96) 0%, rgba(0, 0, 0, 0.4) 50%), var(--url);
}
</style>

<script>
export default {
  name: 'DayCard',
  props: ['text', 'day', 'i', 'url'],
  computed: {
    cssProps () {
      return {
        '--url': `url(${this.url})`
      }
    }
  },
  methods: {
    getId (i) {
      return `day-${i + 1}`
    }
  }
}
</script>
