<template>
  <nav>
    <div class="top-bar">
      <img src="@/assets/images/logo_transparent.png" alt="Movisit logo" id="logo-img">
      <div class="btn-top" tag="li">
        <router-link to="/" class="link-sidebar"><h2>{{ text.sidebar }}</h2></router-link>
      </div>
      <div class="btn-top" tag="li">
        <router-link to="/got" class="link-sidebar"><h2>Game Of Thrones</h2></router-link>
      </div>
      <div class="btn-top" tag="li">
        <router-link to="/downton" class="link-sidebar"><h2>Downton Abbey</h2></router-link>
      </div>
    </div>
  </nav>
  <router-view :text="text"/>
  <button class="link-lang-btn" @click="changeLanguage()">{{text.changeLanguage}}</button>
</template>

<script>
import allTexts from '@/data/text.json'

export default {
  data () {
    return {
      text: allTexts[0],
      language: 'Fr'
    }
  },
  methods: {
    changeLanguage () {
      if (this.language === 'Fr') {
        this.language = 'En'
        this.text = allTexts[1]
      } else {
        this.language = 'Fr'
        this.text = allTexts[0]
      }
    }
  },
  mounted () {
    const userLang = navigator.language || navigator.userLanguage
    if (userLang.includes('fr')) {
      this.language = 'Fr'
      this.text = allTexts[0]
    } else {
      this.language = 'En'
      this.text = allTexts[1]
    }
  }
}
</script>
